.aboutBlockMainContent{
    background-color: #d6b2e4;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

.aboutBlockMainContentArticle{
    margin-top: 40px;
    width: 75%;
    display: flex;
    border: rgb(0, 0, 0) solid 3px;
    margin-bottom: 40px;
    height: 500px;
}
.aboutBlockMainContentPrevButton{
    margin-right: 30px;
}

.aboutBlockMainContentNextButton{
    margin-left: 30px;
}
.aboutBlockMainContentPrevButton, .aboutBlockMainContentNextButton{
    height: 100px;
    width: 100px;
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: center;
    background-color: gray;
    color:#ffffff;
    font-size: 70px;
    font-size: 80px;
    font-weight: 400;
    border: none;
    outline:none;
}

.pageNum{
    position: absolute;
    width: 75%;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.322);
    font-size: 25px;
    font-weight: 500;
    padding-top: 3px;
    padding-bottom: 3px;
    align-self: flex-end;
}

@media only screen and (max-width: 1080px) {
    *{
        margin: 0;
        padding: 0;
    }
    .pageNum{
        width: 100%;
    }
    .aboutBlockMainContentArticle{
        margin: 0;
        height: 0px;
        width: 100%;
        border: black solid 0px;
    }
    .aboutBlockMainContentNextButton {
        
        margin-left: 0px;
        width: 50%;
        height: 10%;
        order: 2;
        align-self: flex-end;
    }
    .aboutBlockMainContentPrevButton {
        margin-right: 0px;
        width: 50%;
        height: 10%;
        order: 3;
        align-self: flex-end;
    }

    .aboutBlockMainContent{
        height: 600px;
        
    }
}


@media only screen and (min-width: 1080px) {
    .aboutBlockMainContentPrevButton:hover, .aboutBlockMainContentNextButton:hover{
        box-shadow: 0 5px 10px  #7d548b;
    }
        .aboutBlockMainContentPrevButton, .aboutBlockMainContentNextButton{
            border-radius: 20px;
    }
}

.aboutBlockMainContentPrevButton:hover, .aboutBlockMainContentNextButton:hover{
    cursor: pointer;

}
